
.navbar{
    position: fixed;
    top:0;
    left: 0;
    height: 10vh;
    width: 100%;
    width: 100%;
    z-index: 2;
    background-color: var(--primary-nav);
}
.nav-link-container{
    width: 95%;
    float: left;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}   
.nav-link-container > .btn{
    display: inline;
    background-color: var(--primary-nav);
    color: white;
    margin-left: 10%;
    background-color:var(--secondary-nav);
    text-align: center; 

}

.nav-link-container > .btn:hover{
    background-color: white;   
    color:var(--primary-nav) 
}
.toggle{
    display: none;
}
.checkbox-container{
    margin-left: auto;
    margin-top: auto;
    margin-bottom: 0;
    margin-right: 0;
}
.checkbox-container input[type="checkbox"]{
    -webkit-appearance: none;
    visibility: hidden;
    display: none;
}
.checkbox-container input[type="checkbox"]:checked ~ .check{
    background: #ffffff;
}
.checkbox-container input[type="checkbox"]:checked ~ .check:before{
transform: translateX(120%);
background: black ;
}

.check::before{
    content: '';
    position: absolute;
    top: 2px;
    left: 2px;
    background:#fff;
    width: 3.4vh;
    height: 3.4vh;
    border-radius: 50%;
    transition:0.5s;
}
.checkbox-container .check{
    display: block;
    position: relative;
    text-align: center; 
    width: 8vh;
    height: 4vh;
    margin-right: 5vh;
    margin-bottom: 0.5vh;
    background:#092c3e ;
    cursor:pointer;
    border-radius: 80vh;
    transition:ease-in 0.5;
}
/*media queries, the bane of every developer*/



.navbar .darkmodetoggle{
    display: inline-block;
    background-color: red;
    height: 10%;
    width: 10%;
    margin-left: auto;
    margin-right: 10%;
    margin-top: auto;
    margin-bottom:0;
}
@media (max-width: 820px) {
    .navbar{
        position: fixed;
        top:0;
        left: 0;
        height: 100vh;
        width: 40vh;
        z-index: 2;
        background-color: transparent;
    }
    
    .nav-link-container > .btn{
        margin-top: 10%;
    }
    .toggle{
        background: url("https://img.icons8.com/material-outlined/24/000000/menu--v1.png");;
        filter: invert(1);
        background-position: center;
        display: inline-block;
        margin-left: 4%;
        margin-top: 2%;
        margin-bottom: auto;
        background-color: transparent;
    }
    .nav-link-container{
        display: none;
        flex-direction: column;
        align-items: flex-start;
        justify-content:flex-start;
        width: auto;
        width: 100%;
        height: 80%;
    }
    

    .navbar .darkmodetoggle{
        display: inline-block;
        width: 5%;
        padding: 5%;
        background-color: red;
    }
}

@media screen and (min-width: 820px) {
    .navbar{
        background-color: var(--primary-nav) !important;
    }
}
