.background-close{
    position: fixed;
    width: 100vw;
    height: 100vh;
    z-index: 6;
    background-color: rgba(0, 0, 0, 0.719);
    top:0;
    left: 0;
}
.expanded-project{
    position: fixed; 
    top: 0;
    left: 0; 
    right: 0;
    margin-top: 5vw; 
    margin-left: auto; 
    z-index: 10;
    margin-right: auto; 
    width: 40vw;
    height: 80vh;
    background-color: rgb(129, 129, 129);
    display: flex;
    text-align: center;
    align-items: center;
    flex-direction: column;
}